.hero {
  padding: 0 5rem;
  display: flex;
  // flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  height: 100vh;

  min-height: 100vh;
  background-color: #e9e9e9;
  @media (max-width: 576px) {
    justify-content: flex-start;
    white-space: nowrap;
  }
  @media (max-width: 414px) {
    padding: 0 2rem;
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      img {
        fill: white;
        filter: brightness(0) invert(1);
      }
    }
  }

  &__content {
    width: 60%;
  }

  &__img {
    width: 50%;
  }

  &__p {
    margin: 0;
    font-size: 7rem;
    @media (max-width: 1235px) {
      font-size: 6rem;
    }
    @media (max-width: 1163px) {
      font-size: 5rem;
    }
    @media (max-width: 765px) {
      font-size: 48px;
    }
    @media (max-width: 670px) {
      font-size: 4rem;
    }
    @media (max-width: 576px) {
      font-size: 5rem;
    }
  }
  &__btn {
    border-radius: 10px;

    // @media (max-width: 575px) {
    //   padding: 3px 6px !important;
    // }
  }
  &__cta {
    display: flex;
    gap: 1rem;

    @media (max-width: 670px) {
      font-size: 1rem;
    }
    @media (max-width: 575px) {
      // font-size: 0.5rem;
    }
  }

  &__imgContainer {
    width: 40%;
    align-self: flex-end;
    @media (max-width: 800px) {
      align-self: center;
    }
    @media (max-width: 576px) {
      display: none;
    }
  }

  &__img {
    display: block;
    margin: 0 auto;
    width: 637px;
    height: 774px;
    background-image: url("../../image/hero.png");
    background-size: cover;
    @media (min-width: 1281px) {
      width: 600px;
      height: 700px;
      margin-left: -15rem;
    }
    @media (max-width: 1280px) {
      width: 600px;
      height: 700px;
      margin-left: -15rem;
    }
    @media (max-width: 1100px) {
      width: 550px;
      height: 650px;
    }
    @media (max-width: 980px) {
      width: 500px;
      height: 600px;
    }
    @media (max-width: 850px) {
      width: 500px;
      height: 600px;
      margin-left: -17rem;
    }
    @media (max-width: 800px) {
      width: 300px;
      height: 300px;
      margin: 0 auto;
      border-radius: 50%;
      margin-left: -2rem;
    }
    @media (max-width: 670px) {
      width: 250px;
      height: 250px;
    }
    @media (max-width: 575px) {
      width: 200px;
      height: 200px;
    }
    &:hover {
      background-image: url("../../image/heroAfter.png");
      background-size: cover;
    }
  }
}
