.navbar {
  box-sizing: border-box;
  width: 100%;
  padding: 8px 40px 8px 50px;
  height: 80px;
  background-color: #e9e9e9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.09);
  position: fixed;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 576px) {
    padding: 8px 20px 8px 20px;
  }
  @media (max-width: 414px) {
    padding: 8px 5px 8px 5px;
  }
  &__h3 {
    color: #2d2e32;
    // cursor: pointer;
    font-weight: 700;
    font-size: 2rem;
    white-space: nowrap;
    @media (max-width: 576px) {
      font-size: 1.7rem;
    }
    @media (max-width: 414px) {
      font-size: 1.5rem;
    }
  }

  &__ul {
    display: flex;
    gap: 1.5rem;
    list-style: none;
    @media (max-width: 576px) {
      padding: 0;
    }
    li {
      color: #2d2e32;
      font-weight: 600;
      font-size: 1.7rem;
      text-decoration: none;
      transition: all 0.25s;
      cursor: pointer;
      @media (max-width: 576px) {
        font-size: 1.5rem;
      }
      @media (max-width: 414px) {
        font-size: 1.3rem;
      }
      &:hover {
        font-weight: 700;
        text-decoration: underline;
        text-decoration-thickness: 3px;
      }
    }
  }
}
