.project {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #e9e9e9;
  padding: 1rem 10rem 1rem 10rem;
  @media (max-width: 1200px) {
    padding: 1rem 4rem 1rem 4rem;
  }
  @media (max-width: 600px) {
    padding: 1rem 1rem 1rem 1rem;
  }

  &__container {
    padding: 5rem 0;
    display: flex;
    gap: 3rem;
    @media (max-width: 1000px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__warp {
    width: 50%;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.25);
    background-color: #fff;
    @media (max-width: 1000px) {
      width: 80%;
    }
    @media (max-width: 800px) {
      width: 90%;
    }
    @media (max-width: 700px) {
      width: 95%;
    }
    img {
      border-radius: 10px 10px 0 0;
      width: 100%;
      max-width: 100%;
      max-height: 100%;
      @media (max-width: 414px) {
        height: 250px;
      }
    }
  }

  &__content {
    h3 {
      margin: 10px 0;
      text-align: center;
      font-size: 26px;
      font-weight: 500;
    }

    h4 {
      margin: 10px 0;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
    }

    p {
      padding: 0 5rem;
      // text-align: left;
      color: #1c1e53;
      text-align: justify;
      font-size: 15px;
      font-style: normal;
      font-weight: 400;
      /* 36px */
    }
  }

  &__btnWarp {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding-bottom: 1rem;

    button,
    a {
      border-radius: 10px;
      border: 2px solid #000;
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
      background-color: #fff;
      padding: 10px 20px;
      text-decoration: none;
      color: black;
    }
    p {
      margin: 0;
    }
  }
}

.projectabout {
  box-sizing: border-box;
  padding: 64px 50px;
  background-color: #4d4d4d;
  height: 202px;
  display: flex;
  justify-content: center;
  align-items: center;

  &__h2 {
    width: 100%;
    margin: 0;
    color: #fff;
    text-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
}
