.skills,
.work {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #e9e9e9;
  padding: 1rem 10rem 1rem 10rem;
  @media (max-width: 1000px) {
    padding: 1rem 7rem 1rem 7rem;
  }
  @media (max-width: 850px) {
    padding: 1rem 6rem 1rem 6rem;
  }
  @media (max-width: 640px) {
    padding: 1rem 5.3rem 1rem 5.2rem;
  }

  @media (max-width: 580px) {
    padding: 1rem 3rem 1rem 3rem;
  }

  &__h2 {
    text-align: left;
    color: #000;
    font-size: 36px;
    font-weight: 700;
    line-height: normal;
    text-decoration-line: underline;
  }

  &__h2Description {
    text-align: left;
    color: #000;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @media (max-width: 864px) {
      font-size: 20px;
    }
  }
}
.uiSkills {
  flex-wrap: wrap;
}
.uiSkills,
.designTool,
.Language {
  display: flex;
  gap: 1rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.container {
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  flex: 1;

  border-radius: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);

  @media (max-width: 670px) {
    padding: 20px;
  }
  @media (max-width: 640px) {
    padding: 15px;
  }
  &__p {
    display: block;
    height: 100%;
    margin: 0;
    @media (max-width: 536px) {
      font-size: 1.4rem;
    }
    @media (max-width: 500px) {
      font-size: 1.3rem;
    }
  }

  &__hr {
    border: none;
    border-top: 1px solid black;
    background-color: #000;
    margin: 0.5rem 0;
  }
}

.starbucks {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  @media (max-width: 580px) {
    gap: 2rem;
  }
  @media (max-width: 414px) {
    flex-direction: column;
  }
  &__img {
    @media (max-width: 765px) {
      width: 80px;
      height: 80px;
    }
  }
  &__p1 {
    color: #000;
    font-family: Helvetica;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 40px;
    /* 111.111% */
    text-align: left;
    @media (max-width: 765px) {
      font-size: 30px;
    }
    @media (max-width: 550px) {
      font-size: 28px;
    }
    @media (max-width: 414px) {
      font-size: 25px;
    }
  }

  &__p2 {
    color: #4d4d4d;
    font-family: Helvetica;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    text-align: left;
    @media (max-width: 825px) {
      font-size: 28px;
    }
    @media (max-width: 765px) {
      font-size: 22px;
    }
    @media (max-width: 550px) {
      font-size: 20px;
    }
  }

  &__pWarp {
  }
}

.work__description {
  color: #000;
  text-align: justify;
  font-family: Helvetica;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  /* 150% */
}

.greenP {
  color: #007042;
}

.experience {
  display: flex;
  width: 100%;
  justify-content: space-evenly;

  gap: 1.5rem;
}
.work__design {
  @media (max-width: 955px) {
    display: none;
  }
}

.work__comment,
.work__design {
  @media (max-width: 600px) {
    display: none;
  }
  display: flex;
  flex-direction: column;

  h3 {
    color: #000;
    text-align: justify;
    font-family: Helvetica;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 36px;
    /* 112.5% */
    text-decoration-line: underline;
  }
}

.work__imgWarp {
  display: flex;
  gap: 0.5rem;

  & > :first-child {
    @media (max-width: 1275px) {
      display: none;
    }
  }

  img {
    max-width: 100%;
  }
}

.work {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #e9e9e9;
  padding: 1rem 10rem 5rem 10rem;
  @media (max-width: 1000px) {
    padding: 1rem 7rem 5rem 7rem;
  }
  @media (max-width: 850px) {
    padding: 1rem 6rem 5rem 6rem;
  }
  @media (max-width: 640px) {
    padding: 1rem 5.3rem 5rem 5.2rem;
  }

  @media (max-width: 580px) {
    padding: 1rem 3rem 5rem 3rem;
  }
}

.work__design {
  @media (max-width: 955px) {
    display: none;
  }
}
