.contact {
  height: 100%;
  padding: 10rem 20rem;
  background-color: #f9f9f9;
  @media (max-width: 1200px) {
    padding: 8rem 10rem;
  }
  @media (max-width: 700px) {
    padding: 5rem 5rem;
  }
  &__h2 {
    font-size: 5rem;

    margin-bottom: 1rem;
    margin-top: 5rem;
    @media (max-width: 800px) {
      margin-top: 3rem;
    }
    @media (max-width: 37.5em) {
      margin-top: 5rem;
    }
  }
  &__p {
    width: 70%;
    margin: 0 auto;
    text-align: left;
    margin-bottom: 2rem;
    @media (max-width: 75em) {
      width: 85%;
    }
    @media (max-width: 500px) {
      font-size: 1.4rem;
    }
  }
}

.contactForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  font-size: 1.6rem;

  .input {
    width: 100%;
    border-radius: 2px;
    max-width: 800px;
    padding: 8px 12px;
    border-width: 1px;
  }

  &__btn {
    margin-top: 1.5rem;
    font-size: $mid-font-size;
  }
}

.haveSubmit {
  height: 100vh;
  padding: 10rem 20rem;
  @media (max-width: 910px) {
    padding: 10rem 10rem;
  }
  @media (max-width: 710px) {
    padding: 10rem 5rem;
  }
  @media (max-width: 710px) {
    padding: 10rem 2rem;
  }
  &__h2 {
    font-size: $big-font-size;
    @media (max-width: 37.5em) {
      font-size: 3.2rem;
    }
    @media (max-width: 25.875em) {
      font-size: 3rem;
    }
  }
  &__p {
    font-size: 2rem;
    @media (max-width: 37.5em) {
      font-size: 1.7rem;
    }
  }
  &__emailAnimation {
    height: 500px;
    width: 500px;
    @include respond(phone) {
      height: 400px;
      width: 400px;
    }
  }
}
