.about {
  box-sizing: border-box;
  width: 100%;
  height: 237px;
  background-color: #4d4d4d;
  padding: 15px 28px;
  color: #fff;

  &__container {
    height: 100%;
    display: flex;
    align-items: center;
    gap: 5rem;
    @media (max-width: 1005px) {
      gap: 3rem;
    }
    @media (max-width: 765px) {
      flex-direction: column;
      gap: 1rem;
    }
  }

  &__h2 {
    width: 35%;
    margin: 0;
    color: #fff;
    text-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    @media (max-width: 1005px) {
      font-size: 45px;
    }
    @media (max-width: 822px) {
      font-size: 40px;
    }
    @media (max-width: 765px) {
      margin-top: 1rem;
      width: 100%;
    }
  }

  &__p {
    width: 50%;
    text-align: left;
    @media (max-width: 765px) {
      width: 95%;
      font-size: 20px;
      margin: 0;
    }
    @media (max-width: 530px) {
      font-size: 17px;
    }
  }
}
